import React, {useState} from 'react'
import "./App.css";
import {Button} from '@material-ui/core'

const App = () => {

    const PAGE_PRODUCTS = 'products';
    const PAGE_CARTS = 'carts';

    const [products] = useState([{
        id:"1",
        plan:"FREE",
        price:"$0",
        month:"/month",
        user:"Single User",
        storage:"5GB Storage",
        projects: "Unlimited Private Projects",
        phone_support:"dedicated Phone Support",
        subdomain:"free Subdomain",
        report:"Monthly Status Report" }
        ,{
        id:"2",
        plan:"PLUS",
        price:"$9",
        month:"/month",
        user:"5 Users",
        storage:"50GB Storage",
        projects: "Unlimited Private Projects",
        phone_support:"Dedicated Phone Support",
        subdomain:"Free Subdomain",
        report:"Monthly Status Report"
        },

        {
        id:"3",
        plan:"PRO",
        price:"$49",
        month:"/month",
        user:"Unlimited Users",
        storage:"150GB Storage",
        projects: "Unlimited Private Projects",
        phone_support:"dedicated Phone Support",
        subdomain:"Free Subdomain",
        report:"Monthly Status Report" 
    }]);

    const [cart, setCart] = useState([]);

    const [page, setPage] = useState(PAGE_PRODUCTS)


    const renderProducts = () =>{


        return(
            <>
                    <section className="pricing py-5">
                        <div className=" container">
                            <div className=" row">
           
                    {products.map((item,idx)=>{
                        return(

                            <div className="col-lg-4">
                                    <div className="card mb-5 mb-lg-0">
                                    <div className="card-body">
                                        <h5 className="card-title text-muted text-uppercase text-center">{item.plan}</h5>
                                        <h6 className="card-price text-center">{item.price}<span class="period">/month</span></h6>
                                        <ul className="fa-ul">
                                        <li><span className="fa-li"><i className="fas fa-check"></i></span>{item.user}</li>
                                        <li><span className="fa-li"><i className="fas fa-check"></i></span>{item.storage}</li>
                                        <li><span className="fa-li"><i className="fas fa-check"></i></span>Unlimited Public Projects</li>
                                        <li><span className="fa-li"><i className="fas fa-check"></i></span>Community Access</li>

                                        {item.plan === "FREE" ? (
                                            <>
                                            <li className="text-muted"><span className="fa-li"><i className="fas fa-times"></i></span>{item.projects}</li>
                                            <li className="text-muted"><span className="fa-li"><i className="fas fa-times"></i></span>{item.phone_support}</li>
                                            <li className="text-muted"><span className="fa-li"><i className="fas fa-times"></i></span>{item.subdomain}</li>
                                            <li className="text-muted"><span className="fa-li"><i className="fas fa-times"></i></span>{item.report}</li>
                                            
                                            <button 
                                            className="btn btn-block btn-primary text-uppercase"
                                            onClick={() => {
                                                setCart([...cart, {...item}])
                                                console.log(cart);
                                            }}
                                            >Add to cart</button>
                                            </>
                                        ) :("")}
                                        
                                        {item.plan === "PLUS" ? (
                                            <>
                                                <li><span className="fa-li"><i className="fas fa-check"></i></span>{item.projects}</li>
                                                <li><span className="fa-li"><i className="fas fa-check"></i></span>{item.phone_support}</li>
                                                <li><span className="fa-li"><i className="fas fa-check"></i></span>{item.subdomain}</li>
                                                <li className="text-muted"><span className="fa-li"><i className="fas fa-times"></i></span>{item.report}</li>
                                                
                                                <button 
                                                className="btn btn-block btn-primary text-uppercase"
                                                onClick={()=>{
                                                    setCart([...cart, {...item} ]);
                                                    console.log(cart);
                                                }}
                                                >Add to cart</button>
                                            </>
                                        ):("")}

                                        {item.plan ==="PRO" ? (
                                            <>
                                                <li><span className="fa-li"><i className="fas fa-check"></i></span>{item.projects}</li>
                                                <li><span className="fa-li"><i className="fas fa-check"></i></span>{item.phone_support}</li>
                                                <li><span className="fa-li"><i className="fas fa-check"></i></span>{item.subdomain}</li>
                                                <li><span className="fa-li"><i className="fas fa-check"></i></span>{item.report}</li>
                                                
                                                <button 
                                                className="btn btn-block btn-primary text-uppercase"
                                                onClick={()=>{
                                                    setCart([...cart,{...item}]);
                                                    console.log(cart);
                                                }}
                                                >Add to cart</button>
                                            </>
                                        ):("")}
                                        
                                        </ul>
                                    </div>
                                    </div>
                            </div>


                        )
                    })}
                                </div>
                    </div>
                    </section>
            </>
        )
    }

    const renderCart = () =>{


        return(
            <>
                    <section className="pricing py-5">
                        <div className=" container">
                            <div className=" row">
           
                    {cart.map((item,idx)=>{
                        return(

                            <div className="col-lg-4">
                                    <div className="card mb-5 mb-lg-0">
                                    <div className="card-body">
                                        <h5 className="card-title text-muted text-uppercase text-center">{item.plan}</h5>
                                        <h6 className="card-price text-center">{item.price}<span class="period">/month</span></h6>
                                        <ul className="fa-ul">
                                        <li><span className="fa-li"><i className="fas fa-check"></i></span>{item.user}</li>
                                        <li><span className="fa-li"><i className="fas fa-check"></i></span>{item.storage}</li>
                                        <li><span className="fa-li"><i className="fas fa-check"></i></span>Unlimited Public Projects</li>
                                        <li><span className="fa-li"><i className="fas fa-check"></i></span>Community Access</li>

                                        {item.plan === "FREE" ? (
                                            <>
                                            <li className="text-muted"><span className="fa-li"><i className="fas fa-times"></i></span>{item.projects}</li>
                                            <li className="text-muted"><span className="fa-li"><i className="fas fa-times"></i></span>{item.phone_support}</li>
                                            <li className="text-muted"><span className="fa-li"><i className="fas fa-times"></i></span>{item.subdomain}</li>
                                            <li className="text-muted"><span className="fa-li"><i className="fas fa-times"></i></span>{item.report}</li>
                                            
                                            <button 
                                            className="btn btn-block btn-primary text-uppercase"
                                            onClick={()=>{
                                                setCart(cart.filter(el => el !== item))
                                             }}
                                            >Remove</button>
                                            </>
                                        ) :("")}
                                        
                                        {item.plan === "PLUS" ? (
                                            <>
                                                <li><span className="fa-li"><i className="fas fa-check"></i></span>{item.projects}</li>
                                                <li><span className="fa-li"><i className="fas fa-check"></i></span>{item.phone_support}</li>
                                                <li><span className="fa-li"><i className="fas fa-check"></i></span>{item.subdomain}</li>
                                                <li className="text-muted"><span className="fa-li"><i className="fas fa-times"></i></span>{item.report}</li>
                                                
                                                <button 
                                                className="btn btn-block btn-primary text-uppercase"
                                                onClick={()=>{
                                                    setCart(cart.filter(el => el !== item))
                                                 }}
                                                >Remove</button>
                                            </>
                                        ):("")}

                                        {item.plan ==="PRO" ? (
                                            <>
                                                <li><span className="fa-li"><i className="fas fa-check"></i></span>{item.projects}</li>
                                                <li><span className="fa-li"><i className="fas fa-check"></i></span>{item.phone_support}</li>
                                                <li><span className="fa-li"><i className="fas fa-check"></i></span>{item.subdomain}</li>
                                                <li><span className="fa-li"><i className="fas fa-check"></i></span>{item.report}</li>
                                                
                                                <button 
                                                className="btn btn-block btn-primary text-uppercase"
                                                onClick={()=>{
                                                   setCart(cart.filter(el => el !== item))
                                                }}
                                                >Remove</button>
                                            </>
                                        ):("")}
                                        
                                        </ul>
                                    </div>
                                    </div>
                            </div>


                        )
                    })}
                                </div>
                    </div>
                    <Button variant="contained" color="secondary"
                    onClick={()=>{
                        if(cart.length === 0){
                            alert("Buy something first");
                        }
                    }}>
                        Buy Now!
                    </Button>
                    </section>
                    
            </>
        )
    }


    return(
        <div className="app">
            <div className = "top_header">
            <button className="products"
            onClick={()=>{
                setPage(PAGE_PRODUCTS);
            }}>
                Products
            </button>
            <button className="cart_btn"
            onClick = {()=>{
                setPage(PAGE_CARTS);
            }}>
                    Go To Cart ({cart.length}) 
                </button>
            </div>

            {page === PAGE_PRODUCTS && renderProducts()}
            {page === PAGE_CARTS && renderCart()}
            
            
        </div>
    )
}
export default App;